import { Component, OnInit } from '@angular/core';
import { BlogService } from '../blog/blog.service';
import { Blog } from '../blog/blog';
import { Title, Meta } from '@angular/platform-browser';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  recommandBlog: Blog[];

  constructor(private blogService: BlogService,
    private titleService: Title,
    private metaService: Meta,) { }

  ngOnInit() {
    this.getRecommandBlog();

    this.titleService.setTitle( '' );
    this.metaService.updateTag({name: 'description', content: '-'})
    this.metaService.updateTag({name: 'keywords', content: ",,,,,,"})
  }

  getRecommandBlog() {
    this.blogService.getBlogList({ page: 1, tags: 'recommand' }).subscribe(res => {
      this.recommandBlog = res.slice(0, 3);
    })
  }

}
